<template>
    <v-app dark>
        <v-app-bar app dark style="
    background: -webkit-radial-gradient(circle, rgba(222, 13, 13, 1) 8%, rgba(205, 20, 20, 1) 54%, rgba(50, 50, 50, 1) 100%);
    background: radial-gradient(circle, rgba(222, 13, 13, 1) 8%, rgba(205, 20, 20, 1) 54%, rgba(50, 50, 50, 1) 100%);
    ">
            <div class="d-flex">
                <v-img alt="YJ Logo" class="shrink mt-8" contain src="../assets/logo.png" transition="fab-transition"
                    width="78" />
            </div>
            <v-toolbar-title class="mt-3" style="font-size: 22px;">
                <span style='font-family: Logo;' class="font-weight-black">YJ | </span>
                <span style='font-family: Logo;' class="font-weight-thin"> Blog</span>
            </v-toolbar-title>
            <div v-if="loggedIn && username != ''" class="ml-10">
                Welcome <span class="font-weight-black">{{ username }}</span>
            </div>
            <v-spacer></v-spacer>
            <v-btn color="transparent" v-if="!loggedIn" @click="dialog = true">
                Login
                <v-icon right dark>
                    mdi-login
                </v-icon>
            </v-btn>
            <template v-else>
                <v-btn color="transparent" @click="new_art = true">
                    New Article
                    <v-icon right dark>
                        mdi-plus-thick
                    </v-icon>
                </v-btn>
                <a class="ml-3 v-btn v-btn--is-elevated v-btn--has-bg theme--dark v-size--default transparent"
                    href="/users_dashboard" target="_blank">
                    Users Dashboard
                    <v-icon right dark>
                        mdi-open-in-new
                    </v-icon>
                </a>
                <v-btn color="transparent" class="ml-3" @click="logout">
                    Logout
                    <v-icon right dark>
                        mdi-logout
                    </v-icon>
                </v-btn>
            </template>
            <v-row justify="center" style="display:none">
                <v-dialog v-model="dialog" persistent max-width="350px" data-aos="zoom-in-up">
                    <v-card>
                        <v-card-title>
                            <span class="text-center justify-center">Login</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-form class="mt-5" ref="form">
                                        <v-col cols="12">
                                            <v-text-field :rules="[rules.email]" prepend-inner-icon="mdi-email"
                                                v-model="email" label="Email" color="#b40000" filled dense required>
                                            </v-text-field>
                                        </v-col>
                                        <v-col cols="12">
                                            <v-text-field :rules="[rules.required]" prepend-inner-icon="mdi-lock"
                                                v-model="password" @click:append="show1 = !show1"
                                                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'" label="Password"
                                                :type="show1 ? 'text' : 'password'" color="#b40000" filled dense>
                                            </v-text-field>
                                        </v-col>
                                    </v-form>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="grey" text @click="dialog = false">
                                Close
                            </v-btn>
                            <v-btn color="#b40000" text @click="login">
                                Login
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>
            <v-row justify="center" style="display:none">
                <v-dialog v-model="new_art" persistent max-width="600px" data-aos="zoom-in-up">
                    <v-card>
                        <v-card-title>
                            <span class="text-center justify-center">Add Article</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-form class="mt-5" ref="form1">
                                    <v-col cols="12">
                                        <v-text-field :rules="[rules.required]" prepend-inner-icon="mdi-rename-box"
                                            v-model="title" label="Title" color="#b40000" filled dense required>
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-textarea filled name="input-7-4" label="Content" :rules="[rules.required]"
                                            dense v-model="content" auto-grow
                                            prepend-inner-icon="mdi-table-of-contents">
                                        </v-textarea>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-checkbox v-model="published" label="Published" color="#b40000" value="true"
                                            hide-details></v-checkbox>
                                    </v-col>
                                </v-form>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="grey" text @click="new_art = false">
                                Close
                            </v-btn>
                            <v-btn color="#b40000" text @click="createArticle()">
                                Create
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>
        </v-app-bar>
        <v-main>
            <v-container>
                <v-row dense align="stretch">
                    <template v-for="(art, index) in articles">
                        <v-col :key="art.id" xl="3" lg="4" md="6" sm="12" xs="12">
                            <v-hover v-slot="{ hover }">
                                <v-card class="mx-auto" color="#b40000" dark max-width="400" data-aos="fade-up"
                                    data-aos-easing="linear" data-aos-duration="1000">
                                    <v-img class="white--text align-end" height="200px"
                                        :src="`https://picsum.photos/500/300?image=${index * 5 + 10}`">
                                        <v-card-title class="text-h6">{{ art.title }}</v-card-title>
                                    </v-img>

                                    <v-card-text class="text-h7 font-weight-bold">
                                        "{{ art.content.slice(0, 80)}}..."
                                    </v-card-text>

                                    <v-card-actions>
                                        <v-list-item class="grow">
                                            <v-list-item-avatar color="grey darken-3">
                                                <v-img class="elevation-6" alt=""
                                                    src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light">
                                                </v-img>
                                            </v-list-item-avatar>

                                            <v-list-item-content>
                                                <v-list-item-title class="font-weight-black">{{art.User.username}}
                                                </v-list-item-title>
                                            </v-list-item-content>

                                            <v-row align="center" justify="end">
                                                <v-expand-transition>
                                                    <div v-if="hover"
                                                        class="transition-fast-in-fast-out darken-2 v-card--reveal white--text">
                                                        <v-btn color="#323232" @click="goTo(art.id)">
                                                            Read more
                                                        </v-btn>
                                                    </div>
                                                    <div v-else>
                                                        <v-icon class="mr-1" small>
                                                            mdi-clock-time-three
                                                        </v-icon>
                                                        <span class="subheading mr-2"
                                                            style="font-size: 14px;">{{art.createdAt.split('T')[0]}}</span>at
                                                        <span class="subheading mr-2"
                                                            style="font-size: 14px;">{{art.createdAt.split('T')[1].split('.')[0]}}</span>
                                                    </div>
                                                </v-expand-transition>

                                            </v-row>
                                        </v-list-item>
                                    </v-card-actions>
                                </v-card>
                            </v-hover>
                        </v-col>
                    </template>
                </v-row>
                <div class="text-center mt-12 mb-12">
                    <v-pagination v-model="page" :length="length" :total-visible="7" color="#b40000"
                        prev-icon="mdi-menu-left" next-icon="mdi-menu-right"></v-pagination>
                </div>
                <message :dialog="creating"></message>
            </v-container>
        </v-main>
    </v-app>
</template>

<script>
    import message from "./message"
    import axios from "axios";
    export default {
        name: 'Home',
        components: {
            message
        },
        data: () => ({
            BaseURL: '',
            token: "",
            articles: [],
            loggedIn: false,
            username: "",
            dialog: false,
            email: "",
            password: "",
            page: 1,
            old_page: 1,
            length: 16,
            start: 0,
            end: 6,
            rules: {
                required: value => !!value || 'Required.',
                counter: value => value.length <= 20 || 'Max 20 characters',
                email: value => {
                    const pattern =
                        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                    return pattern.test(value) || 'Invalid e-mail.'
                },
            },
            show1: false,
            new_art: false,
            title: "",
            content: "",
            published: false,
            creating: false
        }),
        methods: {
            createArticle() {
                if (!this.$refs.form1.validate()) return console.log('Please Validate the form')
                this.creating = true
                this.new_art = false
                setTimeout(() => {
                    this.creating = false
                }, 1500)
                this.creating = true;
                axios.post(`${this.BaseURL}/api/articles/`, {
                    title: this.title,
                    content: this.content,
                    published: this.published
                }, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${document.cookie.split('=')[1]}`
                    }
                }).then((response) => {
                    // console.log(response)
                    if (response.data.id && response.data.title == this.title) {
                        console.log('Article Created !!')
                        this.title=""
                        this.content=""
                        this.articles = []
                        this.getAllArticles()
                    }
                }).catch(error => console.log(error))
            },
            getAllArticles() {
                axios.get(`${this.BaseURL}/api/articles/?offset=${this.start}&limit=${this.end}`, {
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json',
                        }
                    }).then((response) => {
                        // console.log(response)
                        this.articles = response.data.rows;
                        // response.data.rows.forEach(e => {
                        //     e.image = "http://lorempixel.com/400/200/nature/";
                        //     this.articles.push(e)
                        // })
                        this.length = parseInt(parseInt(response.data.count) / this.end);
                    })
                    .catch(error => console.log(error))
            },
            setToken(token) {
                this.token = token;
                this.$store.commit("changeToken", token);
            },
            login() {
                if (!this.$refs.form.validate()) return;
                axios.post(`${this.BaseURL}/api/auth/login`, {
                        email: this.email,
                        password: this.password
                    }, {
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json',
                        }
                    }).then((response) => {
                        // console.log(response)
                        const r = response.data;
                        if (r.error) {
                            console.log(r.error)
                            this.loggedIn = false;
                            return console.log(r.error)
                        }
                        if (r.token)
                            // console.log(r.token)
                            this.setToken(r.token)
                            this.username = r.username
                            console.log('Logged In Successfully') // Signed in successfully
                            this.loggedIn = true;
                            this.dialog = false;
                    })
                    .catch(error => console.log(error))
            },
            logout() {
                this.setToken(null);
                localStorage.removeItem('token')
                this.loggedIn = false;
                window.location = `${this.BaseURL}/api/auth/logout`
            },
            goTo(i) {
                let link = "/article/detail/" + i
                this.$router.push(link)
            },
            check_token(t) {
                // console.log("checking token ", t)
                if (t == 'undefined' || t == "") {
                    this.setToken("");
                    return
                }
                axios.get(`${this.BaseURL}/api/auth/check_token`, {
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json',
                            'Authorization': `Bearer ${this.token}`
                        }
                    }).then((response) => {
                        // console.log(response)
                        if (response.data.error) {
                            console.log("token is not valid")
                            this.setToken("")
                            this.loggedIn = false;
                        }
                        if (response.data.message) {
                            console.log(response.data.message)
                            this.setToken(response.data.token)
                            this.loggedIn = true;
                        }
                    })
                    .catch(error => console.log(error))
            },
            getToken() {
                const t = localStorage.getItem('token');
                if (t != undefined || t != 'undefined' || t != "") this.setToken(t)
                const cookie_token_name = document.cookie.split('=')[0]
                if (cookie_token_name == "token")
                    this.setToken(document.cookie.split('=')[1])
            }
        },
        created() {
            this.getAllArticles()
            this.getToken()
            this.check_token(this.token)
        },
        watch: {
            page: function() {
                // console.log("BEFORE page=", this.page, this.start, this.end)
                // if (this.old_page+1 == this.page){
                //     this.start = (this.old_page + 1)
                // }
                const x = (this.page - this.old_page)
                if (this.old_page < this.page) {
                    this.start = (this.old_page * (6 * x));
                } else {
                    this.start = (this.page * (6 * x));
                }
                if (this.page == 1) {
                    this.start = 0;
                }
                this.old_page = this.page;
                // console.log("AFTER page=", this.page, this.start, this.end)
                this.getAllArticles()
            }
        }
    };
</script>

<style>
    @font-face {
        font-family: Logo;
        src: url(../assets/fonts/Elianto.ttf);
    }
</style>