<template>
  <v-app dark>
    <v-app-bar app dark style="
    background: -webkit-radial-gradient(circle, rgba(222, 13, 13, 1) 8%, rgba(205, 20, 20, 1) 54%, rgba(50, 50, 50, 1) 100%);
    background: radial-gradient(circle, rgba(222, 13, 13, 1) 8%, rgba(205, 20, 20, 1) 54%, rgba(50, 50, 50, 1) 100%);
    ">
      <div class="d-flex" @click="$router.push('/')">
        <v-img alt="YJ Logo" class="shrink mt-8" contain src="../assets/logo.png" transition="fab-transition"
          width="78" />
      </div>
      <v-toolbar-title class="mt-3" style="font-size: 22px;" @click="$router.push('/')">
        <span style='font-family: Logo;' class="font-weight-black">YJ | </span>
        <span style='font-family: Logo;' class="font-weight-thin"> Blog</span>
      </v-toolbar-title>
            <div v-if="loggedIn && username != ''" class="ml-10">
                Welcome <span class="font-weight-black">{{ username }}</span>
            </div>
      <v-spacer></v-spacer>
      <a class="ml-3 v-btn v-btn--is-elevated v-btn--has-bg theme--dark v-size--default transparent"
        href="/users_dashboard" target="_blank">
        Users Dashboard
        <v-icon right dark>
          mdi-open-in-new
        </v-icon>
      </a>
      <template v-if='loggedIn'>
        <v-btn color="transparent" class="ml-3" @click="logout">
          Logout
          <v-icon right dark>
            mdi-logout
          </v-icon>
        </v-btn>
      </template>
    </v-app-bar>
    <v-main>
      <v-container class="mt-5">
        <v-row dense align="stretch">
          <v-col cols="12">
            <v-card class="mx-auto" color="#b40000" dark max-width="100%" data-aos="fade-up" data-aos-easing="linear"
              data-aos-duration="1000">
              <v-row justify="start" style="position:absolute; z-index:10;">
                <v-btn color="#b40000" text class="mt-3 ml-3" icon @click="$router.push('/')">
                  <v-icon large>mdi-keyboard-backspace</v-icon>
                </v-btn>

              </v-row>
              <v-img class="white--text align-end" height="400px"
                :src="`https://picsum.photos/1024/1024?image=${article_id * 5 + 10}`">

                <v-card-title class="text-h6">
                  {{article.title}}
                  <v-row justify="end" v-if="isOwner">
                    <v-btn color="primary" @click='OpenModal'>
                      Update
                    </v-btn>
                    <v-btn color="#b40000" class="ml-2" @click="deleteArticle">
                      Delete
                    </v-btn>
                  </v-row>
                </v-card-title>
              </v-img>

              <v-card-text class="text-h7">
                {{article.content}}
              </v-card-text>

              <v-card-actions>
                <v-list-item class="grow">
                  <v-list-item-avatar color="grey darken-3">
                    <v-img class="elevation-6" alt=""
                      src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light">
                    </v-img>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title class="font-weight-black" v-if='article.User'>{{article.User.username}}
                      ({{article.User.email}})
                    </v-list-item-title>
                  </v-list-item-content>
                  <v-row align="center" justify="end">
                    <v-expand-transition>
                      <div>
                        <v-icon class="mr-1">
                          mdi-clock-time-three
                        </v-icon>
                        <span class="subheading mr-2"
                          v-if='article.createdAt'>{{article.createdAt.split('T')[0]}}</span>
                        <span class="subheading mr-2"
                          v-if='article.createdAt'>{{article.createdAt.split('T')[1].split('.')[0]}}</span>
                      </div>
                    </v-expand-transition>
                  </v-row>
                </v-list-item>
              </v-card-actions>
              <v-row align="center" justify="start">
                <v-expand-transition>
                  <v-chip class="ml-8 mt-1 mb-3" color="#b40000" label text-color="white">
                    <v-icon left color="white">
                      mdi-label
                    </v-icon>
                    Tags :
                    <v-chip class="ma-2" color="white" text-color="#b40000" small>
                      sed
                    </v-chip>
                    <v-chip class="ma-2" color="white" text-color="#b40000" small>
                      quo
                    </v-chip>
                    <v-chip class="ma-2" color="white" text-color="#b40000" small>
                      sit
                    </v-chip>
                    <v-chip class="ma-2" color="white" text-color="#b40000" small>
                      aut
                    </v-chip>
                  </v-chip>
                </v-expand-transition>
              </v-row>
            </v-card>
          </v-col>
        </v-row>
        <v-card class="mx-auto mt-4 mb-10" shaped max-width="100%">
          <v-list two-line dense>
            <v-subheader class="font-weight-black text-center">
              <h1>Comments ({{numberOfComments}})</h1>
            </v-subheader>
            <v-list-item v-for="(item, i) in comments" :key="i">
              <v-list-item-icon class="pt-4">
                <v-list-item-avatar color="#b40000">
                  <v-img class="elevation-6" alt="" src="https://avataaars.io/">
                  </v-img>
                </v-list-item-avatar>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>{{item.content}}</v-list-item-title>
                <v-list-item-subtitle>
                  <v-icon small>mdi-clock</v-icon>
                  {{item.createdAt.split('T')[0]}} at {{item.createdAt.split('T')[1].split('.')[0]}}
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>

            <v-divider class="mt-4"></v-divider>
            <v-list-item class="mt-3">
              <v-text-field v-model="user_comment" append-outer-icon="mdi-send" prepend-icon="mdi-comment-text" filled
                clear-icon="mdi-close-circle" clearable label="Comment" type="text" @click:clear="user_comment = ''"
                color="#b40000" @click:append-outer="createComment">
              </v-text-field>
            </v-list-item>
          </v-list>
        </v-card>
        <v-row justify="center" style="display:none">
          <v-dialog v-model="new_art" persistent max-width="600px" data-aos="zoom-in-up">
            <v-card>
              <v-card-title>
                <span class="text-center justify-center">Update Article</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-form class="mt-5" ref="form1">
                    <v-col cols="12">
                      <v-text-field :rules="[rules.required]" prepend-inner-icon="mdi-rename-box" v-model="user_title"
                        label="Title" color="#b40000" filled dense required>
                      </v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-textarea filled name="input-7-4" label="Content" :rules="[rules.required]" dense
                        v-model="user_content" auto-grow prepend-inner-icon="mdi-table-of-contents">
                      </v-textarea>
                    </v-col>
                  </v-form>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="grey" text @click="new_art = false">
                  Close
                </v-btn>
                <v-btn color="#b40000" text @click="updateArticle()">
                  Update
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'Article',
    components: {},
    data() {
      return {
        BaseURL: '',
        article_id: this.$route.params.id,
        article: {},
        user_title: "",
        user_content: "",
        user_published: "",
        comments: [],
        numberOfComments: 0,
        loggedIn: false,
        token: "",
        username: "",
        user_id: 0,
        user_comment: '',
        new_art: false,
        rules: {
          required: value => !!value || 'Required.',
          counter: value => value.length <= 20 || 'Max 20 characters',
          email: value => {
            const pattern =
              /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            return pattern.test(value) || 'Invalid e-mail.'
          },
        }
      }
    },
    computed: {
      isOwner() {
        if (!this.article.User) return false
        return (this.user_id == this.article.User.id)
      }
    },
    methods: {
      OpenModal() {
        this.user_title = this.article.title
        this.user_content = this.article.content
        this.new_art = true;
      },
      updateArticle() {
        axios.put(`${this.BaseURL}/api/articles/`, {
          id: this.article_id,
          title: this.user_title,
          content: this.user_content,
          published: true
        }, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${document.cookie.split('=')[1]}`
          }
        }).then((response) => {
          if(response.data.updatedAt){
              console.log('Article Updated')
              this.getArticle()
          }
        }).catch(error => console.log(error))
        this.new_art = false;
      },
      deleteArticle() {
        axios.delete(`${this.BaseURL}/api/articles/${this.article_id}`,
        {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${document.cookie.split('=')[1]}`
          }
        }).then((response) => {
            if (response.data.message) this.$router.push('/')
          // }
        }).catch(error => console.log(error))
        
      },
      createComment() {
        axios.post(`${this.BaseURL}/api/comments/`, {
          content: this.user_comment,
          ArticleId: this.article_id,
        }, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${document.cookie.split('=')[1]}`
          }
        }).then((response) => {
          if (response.data.id && response.data.content) {
            console.log('Comment Created !!')
            this.comments = []
            this.getComments()
          }
        }).catch(error => console.log(error))
      },
      setToken(token) {
        this.token = token;
        this.$store.commit("changeToken", token);
      },
      logout() {
        this.setToken(null);
        localStorage.removeItem('token')
        this.loggedIn = false;
        window.location = `${this.BaseURL}/api/auth/logout`
      },
      check_token(t) {
        console.log("checking token ", t)
        if (t == 'undefined' || t == "") {
          this.setToken("");
          return
        }
        axios.get(`${this.BaseURL}/api/auth/check_token`, {
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${this.token}`
            }
          }).then((response) => {
            // console.log(response)
            if (response.data.error) {
              console.log("error token is not valid")
              this.setToken("")
              this.loggedIn = false;
            }
            if (response.data.message) {
              // console.log(response.data)
              this.setToken(response.data.token)
              this.username = response.data.username;
              this.user_id = response.data.id
              this.loggedIn = true;
            }
          })
          .catch(error => console.log(error))
      },
      getToken() {
        const t = localStorage.getItem('token');
        if (t != undefined || t != 'undefined' || t != "") this.setToken(t)
        const cookie_token_name = document.cookie.split('=')[0]
        if (cookie_token_name == "token")
          this.setToken(document.cookie.split('=')[1])
      },
      getArticle() {
        axios.get(`${this.BaseURL}/api/articles/${this.article_id}`, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
          }
        }).then((response) => {
          this.article = response.data;
          // console.log(response)
        }).catch(error => console.log(error))
      },
      getComments() {
        axios.get(`${this.BaseURL}/api/articles/${this.article_id}/comments`, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
          }
        }).then((response) => {
          this.comments = response.data.rows;
          this.numberOfComments = response.data.count;
          // console.log(response)
        }).catch(error => console.log(error))
      }
    },
    created() {
      this.getArticle()
      this.getComments()
      this.getToken()
      this.check_token(this.token)
    }
  }
</script>