import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

const getToken = () => {
    return localStorage.getItem('token');
}

const setToken = (token) => {
    localStorage.setItem('token', token);
}

export default new Vuex.Store({
    state: {
        token: (getToken() != undefined) ? getToken() : "",
    },

    getters: {},

    mutations: {
        changeToken(state, token) {
            state.token = token;
            setToken(token);
        }
    },

    actions: {}
});